<template>
    <div class="application">
        <div class="applicationList" v-if="pageType === 'list'">
            <div class="tableTopTools clearfix">
                <p class="toolName fl">工具栏：</p>
                <div class="buttons fl">
                    <a-button type="primary" @click="optapplication">
                        <a-icon type="plus"></a-icon>添加程序配置
                    </a-button>
                </div>
            </div>
            <div class="tableContent">
                <a-table :columns="columns" :dataSource="applicationData" rowKey="id" :pagination="false">
          <span slot="applicationGroupId" slot-scope="text,record">
            <span>{{record.applicationGroupId | getapplicationGroupCnById(that)}}</span>
          </span>
                    <span slot="gwtCreate" slot-scope="text,record">
            <span>{{record.gwtCreate | dateFormatter(1)}}</span>
          </span>
                    <span slot="gwtModified" slot-scope="text,record">
            <span>{{record.gwtModified | dateFormatter(1)}}</span>
          </span>
                    <span slot="action" slot-scope="text,record">
            <a class="t-optColor-normal" @click="editapplication(record)">编辑</a>
            <a class="t-optColor-warning" @click="deleteapplication(record)">删除</a>
          </span>
                </a-table>
                <div class="ar" style="margin-top:30px" v-if="applicationData.length > 0">
                    <a-pagination
                            v-model="pageParam.page"
                            :total="total"
                            :page-size="pageParam.size"
                            @change="pageChange"
                    ></a-pagination>
                </div>
            </div>
            <applicationModal
                    :applicationVisible="applicationVisible"
                    @applicationOk="applicationOk"
                    @applicationCancel="applicationCancel"
                    :content="content"
                    :optType="optType"
            ></applicationModal>
        </div>
    </div>
</template>
 <script>
     import applicationModal from "@/components/myModal/application-modal.vue";
    export default {
        name: "application",
        components: {applicationModal},
        data() {
            return {
                // 表格列
                columns: [
                    {
                        dataIndex: "tag",
                        title: "标签",
                    },
                    {
                        dataIndex: "keyes",
                        title: "配置名称",
                    },
                    {
                        dataIndex: "value",
                        title: "配置内容",
                    },
                    {
                        dataIndex: "description",
                        title: "配置描述",
                    },
                    {
                        dataIndex: "creator",
                        title: "创建者",
                    },
                    {
                        dataIndex: "gwtCreate",
                        title: "创建时间",
                        scopedSlots: { customRender: "gwtCreate" },
                    },
                    {
                        dataIndex: "gwtModified",
                        title: "最近更新时间",
                        scopedSlots: { customRender: "gwtModified" },
                    },
                    {
                        dataIndex: "action",
                        title: "操作",
                        scopedSlots: { customRender: "action" },
                    },
                ],
                // 表格1数据
                applicationData: [],
                // 操作模态框的显示 隐藏
                applicationVisible: false,
                // 操作模态框的内容
                content: {
                    id: "",
                    tag: "",
                    keyes:"",
                    value:"",
                    description: "",
                    creator: "",
                    gwtCreate: "",
                    gwtModified: "",
                },
                // 模态框的操作类型 0添加 1编辑
                optType: 0,
                pageType: "list", // add是添加/edit编辑 detail是详情 list或者没有是列表
                pageParam: {
                    page: 1,
                    size: 20,
                },
                searchInput: "",
                total: 0,
                that: this
            };
        },
        methods:{
            //  查询程序配置
            getapplication() {
                this.$API.searchBasicRegex(this.pageParam).then((res) => {
                    if (res.code === 1000) {
                        this.applicationData = res.data;
                        this.total = res.total;
                    } else {
                        this.applicationData = [];
                    }
                });
            },
            optapplication() {
                this.resetContent();
                this.applicationVisible = true;
                this.optType = 0;
            },
            //   编辑
            editapplication(column) {
                this.content = column;
                this.optType = 1;
                this.applicationVisible = true;
            },
            // 删除
            deleteapplication(column) {
                const that = this;
                this.$confirm({
                    title: "确定删除该条数据吗？",
                    content: () => '<div style="color:red">删除后将不可恢复</div>',
                    okText: "确认删除",
                    cancelText: "取消删除",
                    onOk() {
                        that.$API.deleteBasicRegex(column.id).then((res) => {
                            if (res.code === 1000) {
                                that.$message.success("删除成功");
                                that.getapplication();
                            } else {
                                that.$message.warning(res.message);
                            }
                        });
                    },
                });
            },
            // 查看详情
            openField(text, column) {
                this.curRow = column;
                this.$router.push({
                    name: `field`,
                    query: {
                        id: column.id,
                        cname: column.cname
                    }
                });
            },
            // 添加application
            applicationOk(val) {
                if (val.optType) {
                    val.formContent={
                        creator: val.formContent.creator,
                        description: val.formContent.description,
                        gwtCreate: val.formContent.gwtCreate,
                        gwtModified: val.formContent.gwtModified,
                        keyes:val.formContent.keyes,
                        tag:val.formContent.tag,
                        value:val.formContent.value,
                    };
                    let param = Object.assign({}, val.formContent, {id: this.content.id});
                    this.$API.updatePrgramConfig(param).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("程序配置更新成功");
                            this.applicationVisible = false;
                            this.getapplication();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                } else {
                    this.$API.addPrgramConfig(val.formContent).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("添加程序配置成功");
                            this.applicationVisible = false;
                            this.getapplication();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                }
            },
            // 取消application
            applicationCancel() {
                this.applicationVisible = false;
                this.resetContent();
            },
            pageChange() {
                this.getapplication();
            },
            // 搜索关键词变化事件
            keywordChange() {
                this.pageParam.page = 1;
                this.getapplication();
            },
            // 清洗content
            resetContent() {
                this.content = {
                    id: "",
                    tag: "",
                    keyes:"",
                    value:"",
                    description: "",
                    creator: "",
                    gwtCreate: "",
                    gwtModified: "",
                };
            },
        },
        created() {
            this.getapplication();
        },
    };
</script>