<template>
  <div class="application-modal">
    <a-modal
            :title="title"
            :visible="applicationVisible"
            @cancel="handleCancel"
            cancelText="取消"
            okText="确定"
            :mask="false"
            :maskClosable="false"
            :width="600"
            class="applicationCls"
    >
      <a-form-model ref="applicationForm" :model="applicationForm" :rules="applicationFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="标签" prop="tag">
              <a-input v-model="applicationForm.tag" placeholder="请输入程序配置标签" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="程序配置名称" prop="keyes">
              <a-input v-model="applicationForm.keyes" placeholder="请输入程序配置名称" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="程序配置内容" prop="value">
              <a-input v-model="applicationForm.value" placeholder="请输入程序配置内容" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="程序配置描述" prop="description">
              <a-textarea v-model="applicationForm.description" placeholder="请输入程序配置描述信息" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
        <a-button type="link" ghost @click="handleCancel">取消</a-button>
        <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
    export default {
        name: "application-modal",
        props: {
            // 模态框的显示与隐藏
            applicationVisible: {
                type: Boolean,
                default: false,
                required: true,
            },
            // 模态框的标题
            title: {
                type: String,
                default: "添加/编辑程序配置",
            },
            // 表单内容 一般添加的时候才会传入
            content: {
                type: Object,
                default: function () {
                    return {
                        id:"",
                        tag: "",
                        keyes: "",
                        value:"",
                        description: "",
                        creator: "",
                        createTime: "",
                        updateTime: "",
                    };
                },
            },
            // 关于操作类型 0为添加 1为编辑
            optType:{
                type:Number,
                default:0,
                required: true,
            }
        },
        data() {
            return {
                applicationFormRule: {
                    tag: [{ required: true, message: "请输入程序配置名称" }],
                    keyes: [{ required: true, message: "请输入程序配置别名" }],
                    value: [{ required: true, message: "请选择分析对象" }],
                },
                applicationForm:{
                    tag: "",
                    keyes: "",
                    description: "",
                    value:""
                },
            };
        },
        methods: {
            handleOk() {
                this.$refs.applicationForm.validate(valid => {
                    if(valid){
                        this.$emit("applicationOk", {formContent:this.applicationForm,optType:this.optType});
                    } else {
                        this.$message.warning('输入有误，请检查');
                        return false
                    }
                })
            },
            handleCancel() {
                this.$emit("applicationCancel", "我点击取消");
            },
        },
        watch:{
            content:{
                handler:function(newVal){
                    Object.assign(this.applicationForm,newVal);
                },
                deep:true
            }
        },
    };
</script>
<style>
  .applicationCls .ant-modal-content,.applicationCls .ant-modal-header {
    background-color: #054273;
    color: #fff;
  }
  .applicationCls .ant-modal-header {
    border-bottom: 1px solid #1890ff

  }
  .applicationCls .ant-modal-header .ant-modal-title{
    color: #fff;
  }
  .applicationCls .ant-modal-footer{
    border-top: 1px solid #1890ff
  }
  .applicationCls .ant-modal-close{
    color: inherit;
  }
</style>